<template>
  <div class="order-container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="6">
          <el-input placeholder="" clearable v-model="orderParams.query" @clear="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" style="width: 100%" border stripe fit>
        <el-table-column type="index" label="#">
        </el-table-column>
        <el-table-column prop="order_number" label="">
        </el-table-column>
        <el-table-column prop="order_price" label="" width="97px">
        </el-table-column>
        <el-table-column prop="order_pay" label="" width="97px">
          <!-- tab -->
          <template slot-scope="scope">
            <el-tag type='danger' effect="plain" color="#FEF0F0" v-if="scope.row.order_pay === '0'">
              
            </el-tag>
            <el-tag type='info' effect="plain" color="#F0F9EB" v-else>
              
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="97px">
        </el-table-column>
        <el-table-column prop="create_time" label="" width="140px">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120px">
          <!-- slot-scope="scope" -->
          <template>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" enterable :hide-after=1000>
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox">
              </el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" enterable :hide-after=1000 >
              <el-button type="success" icon="el-icon-location" size="mini" @click="showLogistics"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="orderParams.pagenum" :page-sizes="[1, 5, 8, 10]" :page-size="orderParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="editClose">
      <el-form :model="addAddressForm" :rules="editRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addAddressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addAddressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="logDialogVisible" width="30%">
      <span>{{ logContext }}</span>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '@/components/order/citydata'
export default {
  name: 'MyOrder',
  data () {
    return {
      // 
      ordersList: [],
      // 
      orderParams: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 8
      },
      // 
      addAddressForm: {
        address1: [],
        address2: ''
      },
      // 
      total: 0,
      //  
      editDialogVisible: false,
      // 
      editRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      // 
      logDialogVisible: false,
      // 
      logContext: []
    }
  },
  created () {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.orderParams
      })
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res)
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange (val) {
      this.pagesize = val
    },
    // 
    handleCurrentChange (val) {
      this.pagenum = val
    },
    // 
    showBox () {
      this.editDialogVisible = true
    },
    // 
    editClose () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async showLogistics () {
      //  1106975712662
      // const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      // console.log(res)
      // if (res.meta.status !== 200) return this.$message.error('')
      this.logDialogVisible = true
      // this.logContext = res.data
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
