<template>
  <div class="addGood-container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">
        </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false">
      </el-alert>
      <!--  -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form :model="addForm" :rules="addFormRules" ref="ruleFormRef" label-width="100px" class="demo-ruleForm"
        label-position="top">
        <el-tabs tab-position="left" style="height: 100%;" v-model="activeIndex" :before-leave="beforeTabLeave"
          @tab-click="tabClicked">
          <!--  -->
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="goods_cat">
              <div class="block">
                <el-cascader v-model="addForm.goods_cat" :options="goodCateList" :props="cateProps"
                  @change="handleChange"></el-cascader>
              </div>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item :label="item.attr_name" v-for="item in manyParamsList" :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox border :label="items" v-for="(items, i) in item.attr_vals" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name + '  :'" v-for="(item, i) in onlyParamsList" :key="i">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="3">
            <!-- action url  -->
            <!-- :file-list="fileList"  -->
            <el-upload :action="upload" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture"
              :headers="headers" :on-success="uploadSuccess">
              <el-button size="small" type="primary"></el-button>
              <!--  -->
              <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce">
            </quill-editor>
            <!--  -->
            <el-button type="primary" class="subBtn" @click="submitContent"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="picDialogVisible" width="80%">
      <img :src="picPreview" alt="" class="picImg">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  name: 'MyAddGood',
  data () {
    return {
      // tab 
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        // 
        attrs: []
      },
      // 
      addFormRules: {
        goods_name: [{ required: true, message: '', trigger: 'blur' }],
        goods_price: [{ required: true, message: '', trigger: 'blur' }],
        goods_weight: [{ required: true, message: '', trigger: 'blur' }],
        goods_number: [{ required: true, message: '', trigger: 'blur' }],
        goods_cat: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      goodCateList: [],
      // 
      cateProps: {
        expandTrigger: 'hover',
        //  
        label: 'cat_name',
        //  id
        value: 'cat_id',
        // 
        children: 'children'
      },
      // 
      // many
      manyParamsList: [],
      // only
      onlyParamsList: [],
      // 
      upload: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 
      headers: {
        Authorization:
          // token
          window.sessionStorage.getItem('token')
      },
      // 
      picPreview: '',
      // 
      picDialogVisible: false
    }
  },
  created () {
    this.getGoodCateList()
  },
  methods: {
    async getGoodCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res)
      this.goodCateList = res.data
    },
    // 
    handleChange () {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // tab
    beforeTabLeave (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // tab
    async tabClicked () {
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: 'many' }
        })
        if (res.meta.status !== 200) return this.$message.error('')
        // console.log(res.data)
        res.data.forEach(item => {
          // console.log(item)
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyParamsList = res.data
        console.log(res.data)
      }
      if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: 'only' }
        })
        if (res.meta.status !== 200) return this.$message.error('')
        // console.log(res.data)
        this.onlyParamsList = res.data
      }
    },
    // 
    handlePreview (file) {
      console.log(file)
      this.picPreview = file.response.data.url
      this.picDialogVisible = true
    },
    // 
    uploadSuccess (response) {
      // console.log(response)
      const picInfo = {
        pic: response.data.tmp_path
      }
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // 
    handleRemove (file) {
      console.log(file)
      const filePath = file.response.tmp_path
      const i = this.addForm.pics.findIndex(x => {
        return x.pic === filePath
      })
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    // 
    submitContent () {
      this.$refs.ruleFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        // goods_cat 
        const addFormCopy = _.cloneDeep(this.addForm)
        addFormCopy.goods_cat = addFormCopy.goods_cat.join(',')
        // 
        this.manyParamsList.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs = newInfo
        })
        // 
        this.onlyParamsList.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs = newInfo
        })
        addFormCopy.attrs = this.addForm.attrs
        // console.log(addFormCopy)
        // -
        const { data: res } = await this.$http.post('goods', addFormCopy)
        if (res.meta.status !== 201) return this.$message.error('')
        console.log(res)
        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-form-item {
  margin: 10px;
}

.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.picImg {
  width: 100%;
}

.subBtn {
  margin-top: 10px;
}
</style>
